  --checkbox-marker-size: 12px;
  --checkbox-radius: 0; }

.gform_wrapper br {
  display: none !important; }

.gform_wrapper ul.gfield_checkbox li input[type=checkbox],
.gform_wrapper ul.gfield_radio li input[type=radio],
.radio,
.checkbox {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0 !important;
  padding: 0 !important;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  visibility: hidden; }

.gform_wrapper ul.gfield_checkbox li input[type=checkbox] + br + label,
.gform_wrapper ul.gfield_radio li input[type=radio] + br + label,
.radio + label,
.checkbox + label {
  display: inline-flex;
  align-items: baseline;
  line-height: var(--body-line-height);
  user-select: none;
  cursor: pointer; }

.gform_wrapper ul.gfield_checkbox li input[type=checkbox] + br + label::before,
.gform_wrapper ul.gfield_radio li input[type=radio] + br + label::before,
input[type=radio] + label::before,
.radio + label::before,
.checkbox + label::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  background-color: var(--color-bg);
  border-width: var(--checkbox-radio-border-width);
  border-color: var(--color-contrast-low);
  border-style: solid;
  background-repeat: no-repeat;
  background-position: center;
  transition: transform .2s, border .2s;
  margin-right: 16px; }

.gform_wrapper ul.gfield_checkbox li input[type=checkbox]:not(:checked):not(:focus) + br + label:hover::before,
.gform_wrapper ul.gfield_radio li input[type=radio]:not(:checked):not(:focus) + br + label:hover::before,
input[type=radio]:not(:checked):not(:focus) + label:hover::before,
.radio:not(:checked):not(:focus) + label:hover::before,
.checkbox:not(:checked):not(:focus) + label:hover::before {
  border-color: var(--color-contrast-medium); }

@supports (grid-area: auto) {
